import { useState } from "react";
import Navbar from "./Navbar";
import axios from "axios";
import Swal from "sweetalert2";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { getToken, getUsername } from "../services/authorize";

const FormComponent = ()=>{
    const [state,setState] = useState({
        title:"",
        author:getUsername()
    })
    const {title,author} = state
    const [content,setContent] = useState('')
    
    const inputValue =  name=>event=>{
        setState({...state,[name]:event.target.value})
    }

    const inputContent = (event)=>{
        setContent(event)
    }
    
    const submitForm = (event)=>{
        event.preventDefault();
        axios.post(`${process.env.REACT_APP_API}/create`,
        {title,content,author},
        {
            headers:{
                authorization:`Bearer ${getToken()}`
            }
        })
        .then(res=>{
            // console.log({title,content,author})
            Swal.fire(
                'Success',
                'Save Content Complete',
                'success'
            )
            setState({title:''})
            setContent('')
        }).catch(err=>{
            console.log(err.response.data)
            Swal.fire(
                'Warning',
                err.response.data.error,         //Alert from server-side => file 'blogController'
                // err.response.data,         //Alert from server-side => file 'blogController'
                'error'
            )
        })
    }
    
    return(
        <div className="container p-5" >
            <Navbar />
            <h1>Create Content</h1>
            {/* {JSON.stringify(state)} */}
            <form onSubmit={submitForm}>
                <div className="form-group">
                    <label className="mb-2">Title</label>
                    <input type='text' className='form-control mb-3' value={title} onChange={inputValue('title')} />
                </div>
                <div className="form-group">
                    <label className="mb-2">Description</label>
                    <ReactQuill value={content} 
                        onChange={inputContent} 
                        theme="snow" 
                        className="mb-3" 
                        placeholder="Write Your Content Here"
                        style={{}}
                    />
                    {/* <textarea className="form-control" value={content} onChange={inputValue('content')}></textarea> */}
                </div>
                <div className="form-group">
                    <label className="mb-2">Author</label>
                    <input type='text' className='form-control' value={author} onChange={inputValue('author')} />
                </div>
                <br/>
                <input type='submit' value='Submit' className="btn btn-primary" />
            </form>


        
        </div>
    )
}

export default FormComponent;