import { useLocation } from "react-router-dom";
import axios from "axios";
import { useState,useEffect } from "react";
import Navbar from "./Navbar";
import renderHTML from "react-render-html";

const SingleComponent = ()=>{
    const location = useLocation();
    const [blog,setBlog] = useState('')
    
    useEffect(()=>{
        axios.get(`${process.env.REACT_APP_API}${location.pathname}`)
        .then(res=>{
            setBlog(res.data)
        })
        .catch(err=>alert(err))
        // eslint-disable-next-line
    },[])

    return(
        <div className="container p-5">
            <Navbar />
            {blog &&        // check if(blog is define) => respond something after &&
                <div>
                    <h1>{blog.title}</h1>
                    <div className="pt-3">{renderHTML(blog.content)}</div>
                    <p className="text-muted">        
                        Author : {blog.author} , Release : {new Date(blog.createdAt).toLocaleString() }
                    </p>
                </div>
            }
        </div>
    )
}

export default SingleComponent;