import { useEffect, useState } from "react"
import Navbar from "./Navbar"
import axios from "axios"
import Swal from "sweetalert2"
import { authenticate, getUsername } from "../services/authorize"
import { useNavigate } from "react-router-dom"

const LoginComponent =() =>{
    const navigate = useNavigate()
    const [state,setState] = useState({
        username:"",
        password:""
    })
    const {username,password} = state

    const inputValue =  name=>event=>{
        setState({...state,[name]:event.target.value})
    }

    const submitForm = (event)=>{
        event.preventDefault()
        axios.post(`${process.env.REACT_APP_API}/login`,{username,password})
        .then(res=>{
            // Login success
            authenticate(res,navigate('/'))
        })
        .catch(err=>{
            Swal.fire(
                'Warning',
                err.response.data.error,         //Alert from server-side => file 'authController'
                'error'
            )
        })
    }

    // If login already => can't open login page
    useEffect(()=>{
        getUsername() && navigate('/')
        // eslint-disable-next-line
    },[])

    return(
        <div className="container p-5" >
            <Navbar />
            <h1>Log in to your account</h1>
            <form onSubmit={submitForm}>
                <div className="form-group">
                    <label>Username</label>
                    <input type='text' className='form-control' value={username} onChange={inputValue('username')} />
                </div>
                <div className="form-group">
                    <label>Password</label>
                    <input type='password' className='form-control' value={password} onChange={inputValue('password')} />
                </div>
                <br/>
                <input type='submit' value='Log in' className="btn btn-primary" />
            </form>

        </div>
    )
}

export default LoginComponent