import {BrowserRouter as Router , Routes , Route } from 'react-router-dom'
import App from './App'
import FormComponent from './components/FormComponent'
import SingleComponent from './components/SingleComponent'
import EditComponent from './components/EditComponent'
import LoginComponent from './components/LoginComponent'
import PrivateRoute from './privateRouteRoute'

const MyRoute = ()=>{
    return(
        <div className='container'>
            <Router>
                <Routes>
                    <Route path='/' exact element={<App/>} />
                    <Route path='/blog/:slug' element={<SingleComponent />} />
                    <Route path='/login' element={<LoginComponent />} />
                    <Route element={<PrivateRoute />} >
                        <Route path='/create' element={<FormComponent/>} />
                        <Route path='/blog/edit/:slug' element={<EditComponent />} />
                    </Route>
                    {/* <Route path='/create' element={<PrivateRoute> <FormComponent/> </PrivateRoute>} /> */}
                    {/* <Route path='/blog/edit/:slug' element={<PrivateRoute> <EditComponent /> </PrivateRoute>} /> */}
                </Routes>
            </Router>
        </div>
        
    )
}
export default MyRoute;