import Navbar from "./components/Navbar";
import axios from "axios";
import { useState,useEffect } from "react";
import { Link } from "react-router-dom"
import Swal from "sweetalert2";
import renderHTML from "react-render-html";
import {getToken, getUsername} from "./services/authorize"

function App() {
  const [blogs,setBlogs] = useState([])
  const fetchData = ()=>{
    axios.get(`${process.env.REACT_APP_API}/blogs`)       // set target
    .then(res=>{
      setBlogs(res.data)        // res function:getAllBlog from '/blogs'
    }).catch(err=>alert(err));
  }
  useEffect(()=>{
    fetchData()
  },[])   // Not catch any effect => just use useEffect when something has change.


  const confirmDelete = (slug) =>{
    const swalMod = Swal.mixin({
      customClass: {
        confirmButton: 'btn btn-danger m-2',
        cancelButton: 'btn btn-success m-2'
      },
      buttonsStyling: false
    })    
    swalMod.fire({
      title: 'Are you sure?',
      text: "You want to delete this content?",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete it!',
      cancelButtonText: 'Cancel!'
    }).then((result)=>{
      if(result.isConfirmed){
        deleteBlog(slug)
      }
    })
  }

  const deleteBlog = (slug) =>{
    axios.delete(`${process.env.REACT_APP_API}/blog/${slug}`,
      {
        headers:{
          authorization:`Bearer ${getToken()}`
        }
      }
    )
    .then(res=>{
      Swal.fire(
        'Complete',
        res.data.message,
        'success'
      )
      fetchData()   // fetch all data after delete
    })
    .catch(err=>console.log(err))
    
  }
  

  return (
    <div className="container p-5" >
      <Navbar/>
      {/* {JSON.stringify(blogs)} */}
      {blogs.map((blog,index)=>(
        <div className="row" key={index} style={{borderBottom:'1px solid silver'}} >
          <div className="col pt-3 pb-2">
            <Link to={`/blog/${blog.slug}`} >
              <h2>{blog.title}</h2>
            </Link>
            <div className="pt-3">{renderHTML(blog.content.substring(0,200))}</div>      {/* show pre-content just 200 chararter & substring is default dunction of JS */}
            <p className="text-muted">        
              Author : {blog.author} , Update : {new Date(blog.updatedAt).toLocaleString() }
            </p>
            {
              getUsername() && (
                <div>
                  <Link to={`/blog/edit/${blog.slug}`} className="btn btn-outline-success" >Edit</Link> &nbsp;
                  <button className="btn btn-outline-danger" onClick={()=>confirmDelete(blog.slug)} >Delete</button>
                </div>
                
              )
            }
            
          </div>
        </div>
      ))}
    </div>
  );
}

export default App;
