import { useEffect, useState } from "react";
import Navbar from "./Navbar";
import axios from "axios";
import { useLocation, useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { getToken } from "../services/authorize";

const EditComponent = ()=>{
    const location = useLocation().pathname.split('/') ;
    const old_slug = location[3]
    // console.log(`slug : ${slug}`)
    
    // Create & set default state
    const [state,setState] = useState({
        title:"",
        author:"",
        slug:""
    })
    const {title,author,slug} = state
    const [content,setContent] = useState('')

    const inputContent = (event)=>{
        setContent(event)
    }

    // Fetch data from API
    useEffect(()=>{
        axios.get(`${process.env.REACT_APP_API}/blog/${old_slug}`)
        .then(res=>{
            const {title,content,author,slug} = res.data
            setState({...state,title:title,author:author,slug:slug})    // take old data from fetch into form
            // Actually, if field & variable is the same name such as => 'title:title' can write just title
            setContent(content)
        })
        .catch(err=>alert(err))
        // eslint-disable-next-line
    },[])

    const showUpdateForm = ()=>(
        <form onSubmit={submitUpdate}>
            <div className="form-group">
                <label className="mb-2">Title</label>
                <input type='text' className='form-control mb-3' value={title} onChange={inputValue('title')} />
            </div>
            <div className="form-group">
                <label className="mb-2">Description</label>
                <ReactQuill value={content} 
                    onChange={inputContent} 
                    theme="snow" 
                    className="mb-3" 
                    placeholder="Write Your Content Here"
                    style={{}}
                />
                {/* <textarea className="form-control" value={content} onChange={inputValue('content')}></textarea> */}
            </div>
            <div className="form-group">
                <label className="mb-2">Author</label>
                <input type='text' className='form-control' value={author} onChange={inputValue('author')} />
            </div>
            <br/>
            <input type='submit' value='Update' className="btn btn-primary" />
        </form>
    )

    // Set present value to state 
    const inputValue =  name=>event=>{
        setState({...state,[name]:event.target.value})
    }    

    const navigate = useNavigate();
    // Update data by API => use PUT Method
    const submitUpdate = (event)=>{
        event.preventDefault();
        axios.put(`${process.env.REACT_APP_API}/blog/${slug}`,{title,content,author},
            {
                headers:{
                authorization:`Bearer ${getToken()}`
                }
            }
        )
        .then(res=>{
            Swal.fire(
                'Success',
                'Update Content Complete',
                'success'
            )
            // const {title,content,author,slug} = res.data
            // setState({...state,title,author,slug})
            // setContent(content)
            navigate('/');              // Redirect
        }).catch(err=>{
            Swal.fire(
                'Warning',
                `Oops, something wrong ${err}`,
                'error'
            )
        })
    }
    
    return(
        <div className="container p-5" >
            <Navbar />
            <h1>Edit Content</h1>
            {showUpdateForm()}
        </div>
    )
}

export default EditComponent;